import { Route, Link } from 'react-router-dom';
import {useEffect, useState, useRef} from "react";
import '../App.css';
import '../css/works.css';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Detail from './detail';
import worksList from '../data/works_list.json';
import MaterialIcon from 'react-google-material-icons';
import { useMediaQuery } from "react-responsive"


export default function Works(){

    //reponsive
    // const isPc = useMediaQuery({
    //     query : "(min-width:1024px)"
    // });
    // const isTablet = useMediaQuery({
    //     query : "(min-width:768px) and (max-width:1023px)"
    // });

    //Detail로 넘어갈때  detail.json 부를 parameter
    const [detailName, setName] = useState('tourdure');

    //back btn (works list 보여주기 / works list는 block  / detail은 none)
    const [whatNone, setNone] = useState(false) 
    // console.log(whatNone);

    //박스 클릭하면 work_div display:none 되고 detail이 보이게하기


    useEffect( () => {} )

    //mouse scroll event
    gsap.registerPlugin(ScrollTrigger);
    const ref = useRef(null);
    useEffect( ()=>{
        const element = ref.current;
        const box = document.querySelectorAll('.front');

        ScrollTrigger.matchMedia({

            "(min-width: 1024px)": function() {
                for(let i=0; i<box.length; i++){
                    let boxFront = `.box${i+1} .front`;
                    let point = (i)*100;
                    // console.log( element.querySelector(boxFront));
                    gsap.to(
                        element.querySelector(boxFront),{
                            x:-465,
                            y:0,
                            scrollTrigger:{
                                trigger : element.querySelector('.work_div'),
                                start : `${point + 10} top`,
                                end:`${point + 300} top`,
                                markers : false,
                                scrub:true
                            }
                        }
                    )
                }
            },


            // reponsive tab
            "(min-width: 768px) and (max-width: 1023px)": function() {
                for(let i=0; i<box.length; i++){
                    let boxFront = `.box${i+1} .front`;
                    let point = (i)*100;
                    // console.log( element.querySelector(boxFront));
                    gsap.to(
                        element.querySelector(boxFront),{
                            x:0,
                            y:-177,
                            scrollTrigger:{
                                trigger : element.querySelector('.work_div'),
                                start : `${point + 10} top`,
                                end:`${point + 300} top`,
                                markers : false,
                                scrub:true
                            }
                        }
                    )
                }
            }
        }); 

    },[] );

    return(
        <div ref={ref}>
            <div className="work_div" style={whatNone ? { display:'none'} : {display : 'block'} }>
                <p className="title">Cherry Workssom</p>
                <div className="works_wrap">
                    {
                        worksList.map((v,k) => {
                            return <div className={`box box${k+1}`}>
                                        <div className="cube back">
                                            <p>{v.title}</p>
                                            <p>{v.what}</p>
                                            <p>Point</p>
                                            <ul>
                                                <li>{v.point1}</li>
                                                <li>{v.point2}</li>
                                                <li>{v.point3}</li>
                                            </ul>
                                        </div>
                                        <div className="cube left"></div>
                                        <div className="cube right"></div>
                                        <div className="cube bottom"></div>
                                        <div className="cube top"></div>
                                        <div className="cube front" data-name={v.parameter} onClick={()=>{setName(v.parameter); setNone(true)}}></div>
                                    </div>
                        } )
                    }
                </div>
            </div>
            <div className="detail_popup" style={ !whatNone ? { display:'none'} : {display : 'block'} }>
                <div className="back" onClick={()=>{setNone(false)}} >
                    <MaterialIcon icon="keyboard_backspace" size={43} style={{color:'black'}}/>
                </div>
                <Detail a={detailName} />
            </div>
        </div>

    )
}
